
@font-face {
    font-family: 'Martian Mono';
    src: url('/fonts/MartianMono-Regular.woff2') format('woff2');
    font-weight: 400;
}
/*
@font-face {
    font-family: 'Martian Mono';
    src: url('/fonts/MartianMono-Bold.woff2') format('woff2');
    font-style: bold;
}
*/
body {
    font-family: 'Martian Mono';
    color: #000;
    font-size: 16px;
    text-align: left;
    word-wrap: break-word;
    overflow-wrap: break-word;
    line-height: 1.5;
    margin: auto;
    padding: 20px;
    background-color: #fff;

}
a {
    color:#3273dc;
}
a:visited {
    color: #8b6fcb;
}

.navigation-header{
    display:grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "logo navigation";
    align-items: center;
    justify-content: stretch;
    text-align: center;
    margin-bottom: 10px;
}

.navigation-header > .logo {
    grid-area: logo;
}

.navigation-header a {
    text-decoration: none;
    color:#000;
}

.navigation-header > .nav-items {
    grid-area: navigation;

}

@media (max-width: 750px){
    .navigation-header {
    grid-template-columns: auto;
    grid-template-areas: "logo"
                        "navigation";
    }

}
.logo{
    font-size: 14px;
    text-decoration: none;
    color:#000;
    border-top: none;
}
nav > a {
    font-size: 20px;
    margin-right: 10px;
    color: #000;
    border-top: 3px solid #000;
}
img {
    padding: 3px;
    border: 3px solid #000;
    max-width: 100%;
    margin: 0 auto;
}

h1,h2,h3,h4,h5{
    color:#000;
}

/*index page*/
.index-page{
    display:grid;
    gap:40px;
    grid-template-columns: auto 12rem minmax(0,38rem) auto;
    grid-template-areas: ". portrait intro .";
}
.index-page > img {
    max-width: 12rem;
    grid-area: portrait;
}
.index-page > div{
    grid-area: intro;
}
@media(max-width: 750px) {
    .index-page {
        gap:0px;
        grid-template-columns: auto;
        grid-template-areas: "portrait"
                             "intro";
    }
    .index-page h2 {
        text-align: center;
    }
}
/*article list*/
.list-page,
.article-page {
   display: grid;
   grid-template-columns: auto minmax(0, 50rem) auto;
   grid-template-areas: ". content .";
}
content { 
    grid-area: content;
    line-height: 1.6;
}
ul.blog-posts {
    list-style-type: none;
    padding: unset;
}
ul.blog-posts li {
    display: flex;
}
ul.blog-posts li span {
    flex: 0 0 130px
}
